.loaderContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  /* width: 50vh;
  height: 50vh; */
  padding-bottom: 100%;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  height: 300px;
  width: 300px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
